




















































































































import Vue from "vue";

import Auction from "../interfaces/Auction";

import TQGlobals from '../classes/TQGlobals';

import IfDebug from "./IfDebug.vue";
import TimeStamp from "./TimeStamp.vue";
import PriceDeviationView from "./PriceDeviationView.vue";


export default Vue.extend({

    props: {
        auction: {
            type: Object as () => Auction,
            required: true
        }
    },

    computed: {
        playerTypedItemName: function () {
            if (!this.auction)
                return null;

            for (let chatToken of this.auction.chatLine.tokens) {
                if (chatToken.type == "ITEM" && chatToken.properties["itemName"] == this.auction.itemName)
                    return chatToken.properties["text"];
            }

            // this shouldn't really happen, but we couldn't find the token for this item in the chat line for some reason
            return this.auction.itemName;
        },

        urlEncodedItemName: function () {
            return encodeURIComponent(this.auction.itemName);
        },

        formattedPrice: function () {
            if (this.auction.price == null)
                return null;
            else
                return TQGlobals.formatNumber(this.auction.price, 0, ".", ",");
        },

        backgroundCssClass: function () {
            return this.auction.isBuying ? "tqItemAuctionWtb" : "tqItemAuctionWts";
        },
    },

    methods: {
    },

    components: {
        IfDebug,
        TimeStamp,
        PriceDeviationView
    }
});
